.sidebar{
    width: 200px;
    height: 100%;
    background: #313a46;
    padding: 30px 0px;
    position: fixed;
  }
  .sidebar ul li a{
    color: #bdb8d7;
    display: block;
  }
  .sidebar .social_media{
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
  }
  .sidebar ul li{
    padding: 15px;
    border-bottom: 1px solid #bdb8d7;
    border-bottom: 1px solid rgba(0,0,0,0.05);
    border-top: 1px solid rgba(255,255,255,0.05);
  }    
  .logo{
    height: 110px;
    width: 133px;
  }
.sidenav{
    height: 112vh;
    padding-left: 20px;
    background-color: black;
    color: white;
    width: 200px;
    position: fixed;
    z-index: 200;
    right: -100%;
    margin-top: -49px;
    -webkit-animation: slide-open .75s forwards;
            animation: slide-open .75s forwards;
}

.sidenav.close {
    right: 0;
    -webkit-animation: slide-close 1.0s forwards;
            animation: slide-close 1.0s forwards;
}

@-webkit-keyframes slide-open {
    100% { right: 0}
}

@keyframes slide-open {
    100% { right: 0}
}

@-webkit-keyframes slide-close {
    100% { right: -100%}
}

@keyframes slide-close {
    100% { right: -100%}
}

